.page-header {
  padding-top: 19px;
  width: 100%;
  z-index: 3;

  &--closed {
    background-color: $dark-blue;
  }

  &--form {
    background-color: $yellow;
    background-image: url("../img/page-header-background-form-mobile.png");
    background-size: 100% 143px;
    background-repeat: no-repeat;
    padding-top: 20px;
  }

  &--catalog {
    padding-top: 20px;
    background-color: $page-header-tablet;
    border-radius: 0 0 20px 20px;
    position: relative;
  }

  &--opened,
  &--nojs {
    background-color: $white;
    padding-top: 27px;
    position: absolute;
    background-image: none;
  }

  &--scrolled {
    background-color: $white;
    background-image: none;
  }
}

.page-header__wrapper {
  min-width: 270px;
  margin: 0 25px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &--closed {
    margin-bottom: 57px;
  }

  &--opened,
  &--nojs {
    margin-bottom: 73px;
  }

  &--scrolled {
    margin-bottom: 32px;
  }
}

.page-header__logo {
  width: 96px;
  height: 15px;
  background-repeat: no-repeat;

  &--closed {
    background-image: url("../img/logo-closed-mobile.svg");
  }

  &--opened,
  &--nojs,
  &--scrolled {
    width: 100px;
    background-image: url("../img/logo-opened-mobile.svg");
  }
}

.page-header__toggle {
  border: none;
  position: relative;
  cursor: pointer;
  width: 25px;
  height: 11px;
  background-color: inherit;
  outline-offset: 7px;

  &--nojs {
    display: none;
  }
}

.page-header__toggle--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 3px;
  background-color: $white;
  border-radius: 1.5px;
  box-shadow: 0 8px 0 0 $white;
}

.page-header__toggle--scrolled::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 3px;
  background-color: $grey;
  border-radius: 1.5px;
  box-shadow: 0 8px 0 0 $grey;
}

.page-header__toggle--opened::before,
.page-header__toggle--opened::after,
.page-header__toggle--nojs::before,
.page-header__toggle--nojs::after {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: $grey;
  box-shadow: none;
  border-radius: 0;
  top: auto;
}

.page-header__toggle--opened::before,
.page-header__toggle--nojs::before {
  transform: rotate(46deg);
}

.page-header__toggle--opened::after,
.page-header__toggle--nojs::after {
  transform: rotate(-46deg);
}

.page-header__title {
  &--closed {
    display: block;
    margin-bottom: 19px;
  }

  &--opened,
  &--nojs {
    display: none;
  }

  &--scrolled {
    color: $dark-blue;
  }
}

.page-header__title--catalog.page-header__title--closed {
  margin-bottom: 15px;
}

.page-header__to-main {
  display: none;
}

@media (min-width: $tablet-width) {
  .page-header {
    padding-top: 41px;

    &--closed {
      background-color: $page-header-tablet;
      border-radius: 0 0 60px 60px;
    }

    &--opened,
    &--nojs {
      padding-top: 41px;
      z-index: 3;
      background-color: $white;
      border-radius: 0;
    }

    &--scrolled {
      background-color: $white;
      border-radius: 0 0 60px 60px;
    }

    &--form {
      background-image: none;
    }
  }

  .page-header__title--catalog.page-header__title--closed {
    margin-bottom: 70px;
  }

  .page-header__wrapper {
    min-width: 688px;
    margin: 0 40px;
  }

  .page-header__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 50px;

    &--closed {
      margin-bottom: 42px;
    }

    &--opened,
    &--nojs {
      margin-bottom: 90px;
    }

    &--scrolled {
      margin-bottom: 42px;
    }
  }

  .page-header__logo {
    width: 166px;
    height: 42px;

    &--closed {
      background-image: url("../img/logo-closed-tablet.svg");
    }

    &--opened,
    &--nojs,
    &--scrolled {
      width: 164px;
      background-image: url("../img/logo-opened-tablet.svg");
    }
  }

  .page-header__toggle {
    justify-self: center;

    &--nojs {
      display: none;
    }
  }

  .page-header__title {
    margin: 87px 0 70px 35px;
  }

  .page-header__to-main {
    display: block;
    margin: 22px 94px 0 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    color: $white;
    text-transform: uppercase;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 32px;
      height: 16px;
      background-repeat: no-repeat;
      background-image: url("../img/to-main-arrow.svg");
      top: 2px;
      right: -60px;
    }

    &--scrolled {
      color: $dark-blue;

      &::after {
        background-image: url("../img/to-main-arrow-scrolled.svg");
      }
    }
  }

  .page-header__to-main-link {
    align-self: center;
    text-decoration: none;

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }

  .page-header__title-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

@media (min-width: $desktop-width) {
  .page-header {
    padding: 48px 0 11px 0;
    background-color: $dark-blue;
    z-index: 5;
    border-radius: 0;

    &--form,
    &--catalog {
      background-color: $page-header-tablet;
      border-radius: 0 0 55px 55px;
    }

    &--scrolled {
      background-color: $white;
      position: relative;
      top: 750px;
      margin-bottom: 19px;
      padding: 21px 0 19px 0;
      border-radius: 0;
    }
  }

  .page-header--scrolled.page-header--form {
    margin-bottom: 260px;
    top: 948px;
  }

  .page-header--scrolled.page-header--catalog {
    margin-bottom: 260px;
    top: 948px;
  }

  .page-header__wrapper {
    width: 1320px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 200px 1fr;
    column-gap: 145px;
    align-items: center;
    position: relative;
  }

  .page-header__toggle {
    display: none;
  }

  .page-header__container {
    margin: 0;
  }

  .page-header__logo {
    width: 200px;
    height: 52px;
    background-image: url("../img/logo-closed-desktop.svg");
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 60px;
      height: 2px;
      border-top: 2px solid $white;
      right: -100px;
      top: 27px;
    }

    &--scrolled {
      width: 198px;
      height: 60px;
      background-image: url("../img/logo-opened-desktop.svg");
      background-size: 198px 60px;

      &::after {
        border-top: 2px solid $site-list-black;
        width: 44px;
        right: -101px;
        top: 30px;
      }
    }
  }

  .page-header__title {
    margin: 122px 0 60px 64px;

    &--scrolled {
      position: absolute;
      top: -871px;
      left: 0;
      color: $dark-blue;
    }
  }

  .page-header__title-container {
    min-width: 1291px;
  }

  .page-header__title--catalog.page-header__title--closed {
    margin-bottom: 59px;
    margin-left: 66px;
  }

  .page-header__to-main {
    margin-top: 68px;

    &--scrolled {
      position: absolute;
      top: -794px;
      right: 29px;
    }
  }
}
